* {
font-family: Arial, Helvetica, sans-serif;

}

input{
    font-size: 1.2rem;
    width: 100%;
    color: gray;
}

.card 
{
    align-content: center;
    border: 1px solid gray;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;

}

.card p {
    padding: 5px;
    text-align: center;
}

.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}